import React from "react";
import { Container, Divider, Grid, Image } from "semantic-ui-react";
import { projects } from "./projectsData";

export default function ListPhone(props) {
  const results = [];
  for (var key = 0; key < Object.keys(projects).length; key += 1) {
    results.push(
      <Grid.Row>
        <Grid.Column>
          <Grid divided columns={2}>
            <Grid.Column>
              <span
                style={{
                  color: "white",
                  fontFamily: "Barlow Semi Condensed",
                  fontSize: "20px",
                }}
              >
                {projects[Object.keys(projects)[key]].name}
              </span>
            </Grid.Column>
            <Grid.Column>
              <span
                style={{
                  color: "grey",
                  fontFamily: "Barlow Semi Condensed",
                  fontSize: "15px",
                  padding: "0px",
                }}
              >
                {projects[Object.keys(projects)[key]].director}
              </span>
              <br></br>
              <span
                style={{
                  color: "grey",
                  fontFamily: "Barlow Semi Condensed",
                  fontSize: "15px",
                  padding: "0px",
                }}
              >
                {projects[Object.keys(projects)[key]].dop}
              </span>
            </Grid.Column>
          </Grid>
        </Grid.Column>
      </Grid.Row>
    );
  }
  return results;
}
