import React, { Component } from "react";
import { Image } from "semantic-ui-react";
import images from "./imageimport";
import "react-responsive-carousel/lib/styles/carousel.min.css";

class ImageComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      impIages: images,
      activeImageIndex: 0,
      imgPath: images[0],
      counter: 0,
      visible: true,
    };
  }

  componentDidMount() {
    console.log("images");
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  componentDidUpdate() {
    if (!this.props.isLoading) {
      setInterval(() => {
        let newActiveIndex =
          this.state.activeImageIndex === 4
            ? 0
            : this.state.activeImageIndex + 1;
        this.setState({
          activeImageIndex: newActiveIndex,
        });
      }, 8000);
    }
  }

  render() {
    if (this.props.isLoading) {
      return (
        <div>
          {this.state.impIages.map((img) => (
            <img style={{ height: "10px" }} src={img} alt="" />
          ))}
        </div>
      );
    } else {
      return (
        <Image
          key={this.state.activeImageIndex}
          className={"elementToFadeInAndOut frontImage"}
          src={this.state.impIages[this.state.activeImageIndex]}
        />
      );
    }
  }
}
export default ImageComponent;
