import React from "react";
import { Image } from "semantic-ui-react";

export default function HoverImagePhone(props) {
  function over(e) {
    e.currentTarget.src = props.hoverImage;
  }
  function out(e) {
    e.currentTarget.src = props.normalImage;
  }
  return (
    <Image
      style={{
        flex: "1",
        backgroundColor:
          "linear-gradient(to top, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.25))",
        height: "100%",
        position: "absolute",
        left: "0px",
        width: "100%",
        overflow: "hidden",
        objectFit: "cover",
      }}
      src={props.normalImage}
      onMouseEnter={over}
      onMouseOver={over}
      onMouseOut={out}
      onTouchStart={over}
      onTouchEnd={out}
      alt=""
    />
  );
}
